.nav{
    top: 50px;
    right: 10px;
    position: fixed;
    display: flex;
    flex-direction: column;
    z-index: 1;
    background-color: white;
    padding: 10px 20px;
    border-radius: 5px;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(50, 50, 93, 0.25),
    0 -6px 16px -6px rgba(50, 50, 93, 0.25);
}
@media screen and (max-width:500px){
    .nav{
        width: 100%;
        bottom:0 ;
        right: initial;
        left: initial;
        top: initial;
    }
}
.nav a{
    text-decoration: none;
    color: #0008fc;
    text-transform: uppercase;
    font-size: 12px;
    text-align: center;
    font-weight: 600;
}
.nav a:not(:last-child){
    margin-bottom: 12px;
}